#quiz{
    /* font-family: 'NanumPen'; */
    font-size: x-large;
    position: relative;
    background-color: #fffcf8;
    /* width: 100%; */
    /* min-height: 200px; */
    padding: 5%;
}

#quiz .answer{
    position: absolute;
    bottom: 10%;
    right: 10%;
    width: 300px;
}

#board{
    background-color: rgb(253, 255, 235);
    height: 80px;
}

#board .board-count{
    position: relative;
    top:25px;
    font-size: x-large;
    text-align: center;
    align-items: center;
}

#wrongs{
    min-height: 400px;
    background-color: lightgoldenrodyellow;
    padding: 5%;
}

#wrongs .wrongs-wrong{
    height: 40px;
}
#wrongs .wrongs-wrong h3{
    text-align: center;
    position: relative;
    top:30%;
}